	transform: translate(-50%, 0%) rotate(45deg);
	-webkit-animation: ScrollingArrow_fade_move_down__H21Bg 4s ease-in-out infinite;
	animation:         ScrollingArrow_fade_move_down__H21Bg 4s ease-in-out infinite;
}

@-webkit-keyframes ScrollingArrow_fade_move_down__H21Bg {
	0%   { -webkit-transform:translate(0,-10px) rotate(45deg); opacity: 0;  }
	50%  { opacity: 1;  }
	100% { -webkit-transform:translate(0,10px) rotate(45deg); opacity: 0; }
}
@keyframes ScrollingArrow_fade_move_down__H21Bg {
	0%   { -webkit-transform:translate(0,-10px) rotate(45deg); transform:translate(0,-10px) rotate(45deg); opacity: 0;  }
	50%  { opacity: 1;  }
