.avatar {
	border-radius: 3px;
    display: inline-block;
    line-height: 1;
    overflow: hidden;
	vertical-align: middle;
	min-width: 200px;
	height: 200px; 
}

.rounded {
	border-radius: 6px!important;
}

.section {
	margin: 10px;
	margin-bottom: 6px;
    width: 70%;
    margin-left: auto;
	margin-right: auto;
	flex-wrap: wrap;
	color: black;
}

.title {
	margin-left: 10px;
	color: black;
	font-family: Karla, sans-serif;
}

.row {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	align-items: baseline;
}

.alignTop {
	align-items: start;
}

.nowrap {
	flex-wrap: nowrap;
}

.wrap {
	flex-wrap: wrap;
}

.flex {
	display: flex;
}

.justifyCenter {
	justify-content: center;
}

.col1 {
	flex-grow: 1;
	margin: 10px;
}

.col2 {
	flex-grow: 2;
	margin: 10px;
}

.col3 {
	flex-grow: 3;
	margin: 10px;
}

.justifyRight {
	justify-content: right;
}

.largeImage {
	width: 400px;
}

.floatRight {
	float: right;
}

.floatLeft {
	float: left;
}

.alignRight {
	text-align: right;
}

.alignCenter {
	text-align: center;
}

.imageText {
	text-align: right;
	font-style: italic;
	font-size: smaller;
}

.smallWidth {
	max-width: 400px;
	width: 70vw;
}

.fullWidth {
	width: 100%;
}

.display {
	display: block !important;
}

.hide {
	display: none !important;
}

.inline {
	display: inline-block;
}

.grey {
	background-color: grey;
}

.leftMargin {
	margin-left: 10px;
}

.leftMarginAuto {
	margin-left: auto;
}

.rightMarginAuto {
	margin-right: auto;
}

.noTopMargin {
	margin-top: 0px;
}

.marginTop3 {
	margin-top: 3px;
}

.noBottomMargin {
	margin-bottom: 0px;
}

.marginTop40 {
	margin-top: 40px;
}

.marginBottom80 {
	margin-bottom: 80px;
}

.margin10 {
	margin: 10px;
}

.marginRight25 {
	margin-right: 25px;
}

.plusCursor {
	cursor: url("data:image/png;base64,") 25 25, auto;
}

.crossCursor {
	cursor: url("data:image/png;base64,") 25 25, auto;
}